@charset "utf-8";

*{
  margin: 0;
  padding: 0;
}
ul,li,ol{
  list-style: none;
}
a{
  text-decoration: none;
  color: #333;
}
/* 헤더영역 */
.header{
  width: 100%;
}
.h_banner{
  width: 100%;
  text-align: center;
  background-color: #5f0080;
  color: #fff;
  line-height: 42px;
  font-size: 14px;
}
.h_top,.h_bottom{
  width: 1050px;
  margin: 0 auto;
}
.h_top > ul{
  display: flex;
  justify-content: flex-end;
}
.txt_color1{
  color: #5f0080;
}
.txt_color2{
  color: #ccc;
}
.h_bottom{
  display: flex;
  justify-content: space-between;
}
.h_top > ul > li{
  margin-right: 5px;
  padding: 15px 0px;
}
.h_top > ul > li >a{
  padding-right: 5px;
  border-right: 1px solid #ccc;
}
.h_top > ul > li:last-child a{border: none;}

.h_con1{
  width: 260px;
  display: flex;
}
.h_con1 > h1{
  width: 100px;
}
.h_con1 > h1> a{
  display:block;
  width: 100%;
}
.h_con1 > h1 > a>img{
  width: 100%;
}
.h_con1 >ul{
  display: flex;
}
.h_con1 >ul >li{
  margin-top: 20px;
}
.h_con1 >ul >li >a{
  display: block;
  padding:0px 10px;
  border-right: 1px solid #ccc;
}
.h_con1 >ul >li:last-child a{border: none;}

.h_con2{
  width: 400px;
  position: relative;
  padding-top: 10px;
}
#search{
  width: 100%;
  height:45px;
  text-indent: 20px;
  border: 1px solid #5f0080;
  border-radius: 5px;
  outline: none;
  position: absolute;
  left: -50px;
}
.h_con2 > label{
  position: absolute;
  right: 60px;
  top: 20px;
  color: #5f0080;
}
.h_con3{
  width: 150px;
}
.h_con3 > ul{
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
}

.h_nav{
  padding-top: 10px;
  width: 1050px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.n_con1{
  width: 100px;
  display: flex;
}
.n_con2{
  width: 750px;
}
.n_con2 >ul{
  display: flex;
  justify-content: space-around;
}
.n_con2 > ul >li a:hover{
  color: #5f0080;
}
.n_con3{
  width: 123px;
  border: 1px solid #ccc;
  border-radius: 10px;
  text-align: center;
}
.n_con3 > span{
  font-size: 14px;
}
/* 로그인 영역 */
.login{
  width: 500px;
  margin: 0 auto;
  text-align: center;
}
.login h3{
  margin: 30px 0px;
}
#id, #pw{
  width: 340px;
  height: 50px;
  text-indent: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 2px;
}
#id{
  margin-bottom: 8px;
}
.l_btn,.r_btn{
  width: 340px;
  height: 50px;
  margin-bottom: 10px;
  border-radius: 5px;
  font-size: 16px;
}
.l_btn{
  background-color: #5f0080;
  color: #fff;
}
.r_btn{
  background-color: #fff;
  color: #5f0080;
  border: 1px solid #5f0080;
}
::placeholder{
  text-indent: 20px;
}
.find_box{
  margin: 0 auto;
  width: 340px;
  position: relative;
  margin-bottom: 20px;
}
.find{display: flex;justify-content: flex-end;}
.find > li{
  margin: 5px;
}
.find > li:first-child{border-right: 1px solid #ccc;
padding-right: 7px;}
.find > li > a{
  font-size: 14px;
}